import React from 'react'
import './Home.css'
import Header from "./PageComponents/Header"
import TextField from "./PageComponents/TextField"


function Home() {
    return (
        <div >
          
            <section className="home">
              <Header/>
              <div className="textbox">
                <TextField/>
              </div>
            </section>

         
        </div>
    )
}

export default Home
